<template>
  <LayoutAuthenticated>
        <!-- LOADING -->
        <SectionMain v-if="loading">
          <LoadingComponent />
        </SectionMain>

    <SectionMain v-else>
      <BreadCrumb :projectName="dashboard.name" class="py-4" />
      <!--PROJECT NAME HEADER-->
      <ProjectName
            :project="db.json.dashboardName"
            :tagline="db.json.dashboardDescription"
            :sector="db.json.dashboardName"
            :logo="dashboard.logo ? dashboard.logo : 'https://cdn4.iconfinder.com/data/icons/leto-weather/64/na_not_available_weather_sun-512.png'"
          />

          <!--TABLE-->
          <CardBox has-table>
            <div class="overflow-x-auto">
            <table>
              <thead class="text-xs text-gray-700 uppercase dark:text-gray-400">
                <tr> 
                  <th />
                  <th>Project</th>
                  <th v-for="metric in metrics" :key="metric.id" @click="sortTable(metric.id)">
                    {{ metricNames[metric.id] }}
                </th>
                </tr>
              </thead> 
              <tbody>
                <tr v-for="coin in assetsPaginated" :key="coin.symbol">
                <th data-label="logo" class="h-6 w-6">
                  <div class="h-6 w-6">                        
                    <img :src="assets[coin.symbol].logo ? assets[coin.symbol].logo : 'https://cdn3.iconfinder.com/data/icons/meteocons/512/n-a-512.png'" alt="Logo" 
                    class="rounded-full">
                    </div>
                    </th>
                    <td data-label="Name" class="w-32">
                      <div class="w-32">                        

                        <router-link :to="`/projects/${coin.symbol}`">{{ coin.name }}</router-link>
                        </div>
                    </td>

                  <td v-for="metric in metrics" :key="metric.id" class="lg:w-1 whitespace-nowrap">
                    <small
                class="text-gray-500 dark:text-slate-400"
                > <span v-html="formatValue(coin.symbol, metric.id)"></span></small>
                    </td>
                </tr>
      
              </tbody> 
              <!--FOOTER -->
                <tfoot class="text-xs text-gray-700 uppercase dark:text-gray-400">
                <tr>
                  <th />
                  <th>Project</th>
                  <th v-for="metric in metrics" :key="metric.id" @click="sortTable(metric.id)">
                    {{ metricNames[metric.id] }}
                </th>
                </tr>
              </tfoot>
            </table>
                  <!--PAGINATION--> 
      <div class="p-3 lg:px-6 border-t border-gray-100 dark:border-slate-800">
        <BaseLevel>
          <BaseButtons>
            <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Previous" @click="currentPage = Math.max(currentPage - 1, 0)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
              <BaseButton
                class="text-xs text-gray-700 uppercase dark:text-gray-400"
                v-for="page in pagesList.slice(Math.max(currentPage - 1, 0), Math.min(currentPage + 1, numPages))"
                :key="page"
                :active="page === currentPage"
                :label="page + 1"
                :color="page === currentPage ? 'lightDark' : 'whiteDark'"
                small
                @click="currentPage = page"
              />
              <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Next" @click="currentPage = Math.min(currentPage + 1, numPages - 1)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
        </BaseButtons>

          <small>Page {{ currentPageHuman }} of {{ numPages }}</small>
        </BaseLevel>
      </div>
      </div>
    </CardBox>
              <!--CHARTS-->
              <div class=" py-4 mb-6 flex overflow-x-auto">
            <CardBox has-table class="w-1/2 mr-2">
                <ChartComponent :firstchart="'Ethereum'" :secondchart="'Polygon'"/>
            </CardBox>
            <CardBox has-table class="w-1/2 mr-2">
                <ChartComponent :firstchart="'Binance'" :secondchart="'Cosmos'"/>
            </CardBox>
          </div>

    </SectionMain> 
  </LayoutAuthenticated>
    
  </template>
  <script>
  import LayoutAuthenticated from "@/layouts/LayoutAuthenticated.vue";
  import SectionMain from "@/components/SectionMain.vue";
  import BreadCrumb from "@/components/BreadCrumb.vue";
  import ProjectName from "@/components/ProjectName.vue";
  import { websitename } from "../supabase"
  import CardBox from "@/components/CardBox.vue";
  import { supabase } from "../supabase";
  import { metricNames } from "@/stores/metricNames";
  import ChartComponent from "@/components/ChartComponent.vue";
  import LoadingComponent from "@/components/LoadingComponent.vue";
  import BaseLevel from "@/components/BaseLevel.vue";
  import BaseButtons from "@/components/BaseButtons.vue";
  import BaseButton from "@/components/BaseButton.vue";    
  export default {
    components: {
    LayoutAuthenticated,
    SectionMain,
    BreadCrumb,
    ProjectName,
    CardBox,
    ChartComponent,
    LoadingComponent,
    BaseLevel,
    BaseButtons,
    BaseButton
},
  data() {
  return {
    assetsPaginated: [],
    currentPage: 0,
    perPage: 10,
    loading: true,
    sortKey: null,
    sortAscending: true,
    coins: {},
    metrics: {},
    assets: {},
    db: {},
    dashboard:
      {
        name: "My Dashboards",
        tagline: "Create your own personalized dashboards to track the data that matters most to you.",
        logo: "https://cdn1.iconfinder.com/data/icons/webina-seo-development-and-marketing/128/seo_web-62-512.png",
        project: 1,
        sector: websitename
      },
    metricNames: metricNames
    }
  },
  async created() {
    // Fetch data when the component is first created
    await this.fetchData();
    await this.fetchAssetData();
    await this.getLogo();
    await this.updateAssetsPaginated();
  },
  watch: {
    currentPage() {
      this.assetsPaginated = this.coins.slice(
        this.perPage * this.currentPage,
        this.perPage * (this.currentPage + 1)
      );
    }
  },
  computed: {
    numPages() {
      if (this.coins.length === 0) {
        return 0;
      }
      return Math.ceil(this.coins.length / this.perPage);
    },
    currentPageHuman() {
      return this.currentPage + 1;
    },
    pagesList() {
      const pagesList = [];
      for (let i = 0; i < this.numPages; i++) {
        pagesList.push(i);
      }
      return pagesList;
    },

  },
  methods: {
    async getLogo() {
        for (let coin of this.coins) {
            let { data: asset, error } = await supabase
                .from('ultimateframe')
                .select('logo')
                .eq('symbol', coin.symbol)

            if (error) {
                console.error("Error fetching logo:", error);
                continue;
            }
            if (!asset[0]) {
                console.error("No logo found for coin:", coin.symbol);
                continue;
            }
            this.assets[coin.symbol] = {...this.assets[coin.symbol], logo: asset[0] ? asset[0].logo : 'https://cdn4.iconfinder.com/data/icons/leto-weather/64/na_not_available_weather_sun-512.png' }
        }
        console.log("ASSETS: ", this.assets)
        console.log("METRICS: ", this.metrics)

    },
    updateAssetsPaginated() {
      const start = this.perPage * this.currentPage;
      const end = start + this.perPage;
      this.assetsPaginated = this.coins.slice(start, end);
      this.loading = false;

    },
  async fetchData() {
  // Make HTTP request to Supabase API to get the dashboard data for the user
  try {
    let { data: dashboard, error } = await supabase
    .from('dashboard')
    .select()
    .eq('id', this.$route.params.id)
  
    // Update values
    this.db = dashboard[0]
    this.coins = dashboard[0].json.selectedCoins
    this.metrics = dashboard[0].json.selectedMetrics  
  
  } catch (error) {
    console.error("Following errors were found: ", error);
    this.errors = error.message;
  }
  },
  async fetchAssetData() {
  try {
    // Fetch the asset data for all coins from the Supabase DB
    let { data: jsoncoin, errors } = await supabase
      .from('jsoncoin')
      .select('payload')
      .order('id', { ascending: false });

    if (errors) return console.error("Error:" + errors);

    for (let coin of this.coins) {
      // Filter the dataset for the specific symbol
      let filteredDataset = jsoncoin[0]['payload'].filter(item => item.symbol === coin.symbol);

      // Update the assets object with the data for the current coin
      this.assets = Object.assign({}, this.assets, { [coin.symbol]: filteredDataset[0] });
    }
    } catch (error) {
      console.error("Following errors were found: ", error);
      this.errors = error.message;
    }
  },
  formatValue(coin, metric) {
    // Get the value from the assets object
    let value = this.assets[coin] && this.assets[coin] ? this.assets[coin][metric] : 'N/A';
  
    // Check if the value is null
    if (value === undefined) {
      return 'N/A';
    }
  
    // Check if the metric is one of the metrics that should be formatted as currency
    if ([
        'price', 'price_btc', 'price_eth', 
        'marketcap', 'liquid_marketcap_usd',
        'volume_last_24_hours', 'real_volume_last_24_hours',
        'fee_total24h', 'fee_total7d', 'fee_totalAlltime', 
        'tvl',
        'all_time_high', 'cycle_low',

].includes(metric)) {
      // Round the value to two decimals and format it as currency
      return Number(value).toLocaleString('en-US', { style: 'currency', currency: 'USD' });
    }
  
    // Check if the metric is one of the metrics that should be formatted as percentage
    if ([
    'roi_2011_usd_percent','roi_2012_usd_percent','roi_2013_usd_percent','roi_2014_usd_percent','roi_2015_usd_percent','roi_2016_usd_percent','roi_2017_usd_percent','roi_2018_usd_percent','roi_2019_usd_percent','roi_2020_usd_percent','roi_2021_usd_percent','roi_2022_usd_percent',
    'percent_down_since_ath', 'percent_up_since_cycle_low', 
    'percent_change_btc_last_24_hours', 'percent_change_btc_last_1_week', 'percent_change_btc_last_1_month', 'percent_change_btc_last_3_months', 'percent_change_btc_last_1_year', 
    'percent_change_eth_last_24_hours', 'percent_change_eth_last_1_week', 'percent_change_eth_last_1_month', 'percent_change_eth_last_3_months', 'percent_change_eth_last_1_year',
    'percent_change_usd_last_24_hours', 'percent_change_last_1_week', 'percent_change_last_1_month', 'percent_change_last_3_months', 'percent_change_last_1_year', 
    'percent_change_month_to_date', 'percent_change_quarter_to_date', 'percent_change_year_to_date',
    'fee_change_1d', 'fee_change_7d', 'fee_change_1m', 
    'tvl_change_1h', 'tvl_change_1d', 'tvl_change_7d'
      ].includes(metric)) {
      // / Round the value to two decimals and format it as percentage
      return `<span class="${value >= 0 ? 'positive' : 'negative'}">${Number(value).toFixed(2)}%</span>`;
      }  
      
    // For all other metrics, round the value to two decimals and format it with a thousand separator
    return Number(value).toLocaleString('en-US');
  },
  sortTable(metric) {
  this.sortKey = metric;
  this.sortAscending = !this.sortAscending;
  
  this.coins.sort((a, b) => {
    // Sort N/A values to the bottom
    if (!this.assets[a] || !this.assets[a] || !this.assets[a][metric]) {
      return 1;
    }
    if (!this.assets[b] || !this.assets[b] || !this.assets[b][metric]) {
      return -1;
    }
  
    let valueA = this.assets[a][metric];
    let valueB = this.assets[b][metric];
  
    if (this.sortAscending) {
      return valueA - valueB;
    } else {
      return valueB - valueA;
    }
  });
  }
  },
  };
  </script>
  <style>
  .positive {
  color: green;
  }
  
  .negative {
  color: red;
  }
  </style>
  